import React, {useState} from 'react'

const computer_number = Math.floor(Math.random() * 1000);
console.log(computer_number)

function App() {
  const [number_of_guesses, setNumberOfGuesses] = useState(10);
  const [userHasWon, setUserHasWon] = useState(false);
  const [advice, setadvice] = useState("");
  const [userGuess, setUserGuess] = useState(0);
  

  function getAnswer(e) {
    if (e.target.value === "" || isNaN(e.target.value)) return;
    setUserGuess(prevVal => e.target.value);
    e.target.value = "";
  }

  

  function checkAns() {
    if (userGuess > computer_number)  {
      setadvice("Lower");
      setNumberOfGuesses(prevVal => prevVal-1);
    }
    else if (userGuess < computer_number) {
      setadvice("Higher");
      setNumberOfGuesses(prevVal => prevVal-1);
    } 
    else setUserHasWon(true)
  }

  function triggerEnter(e) {
    e.code === "Enter" && checkAns()
  }

  function playAgain() {
    window.location.reload();
  }

  return (
    <main>
      <div className="card">
        <div className="top">
          <p>Guess the number</p>
        </div>
        
        {userHasWon && <p>You have won</p>}
        {number_of_guesses === 0 && <p>You're out of guesses ! Correct Answer was {computer_number} </p>}
        {(!userHasWon && number_of_guesses !== 0) && <><p>I'm thinking of a number between 0 and 1000, can you guess it ?</p><p>You have <strong> {number_of_guesses} </strong> {number_of_guesses !== 1 ? "guesses" : "guess"} left</p> <h1>{advice}</h1> 
        <input onKeyDown={triggerEnter} type="number" placeholder="Your guess here..." onChange={getAnswer} min="0" />
        <button onClick={checkAns}>Submit</button></>
         }
         {(userHasWon || number_of_guesses === 0) && <button onClick={playAgain}>Play Again</button>}
      </div>
    </main>
  );
}

export default App;
